<template>
	<div id="nav" v-bind:class="visibility">
		<router-link to="/">
			<img src="../../assets/images/logotip.png"/>
		</router-link>
		<navbar-items></navbar-items>
	</div>
</template>

<script>
	import NavbarItems from "./NavbarItems";
	export default {
		components: {NavbarItems}, name: 'Navbar',
		data () {
			return {
				isVisible: false
			}
		},
		computed: {
			visibility: function () {
				let cl = 'navbar';

				if (this.isVisible) cl += ' visible';
				else cl += ' hidden';

				return cl;
			}
		},
		methods: {
			handleScroll: function (event) {
				let elem = document.getElementsByClassName('show-navbar')[0];

				if (elem) {
					let trigger = elem.offsetTop;

					this.isVisible = window.pageYOffset >= trigger ? true : false;
				}
			}
		},
		watch:{
			$route (to, from){
				this.handleScroll();
			}
		},
		created () {
			window.addEventListener('scroll', this.handleScroll);
		},
		mounted () {
			this.handleScroll();
		},
		destroyed () {
			window.removeEventListener('scroll', this.handleScroll);
		}
	}
</script>

<style lang="scss">
	.navbar {
		position: fixed;
		top: 0;
		width: 100%;
		border-bottom: lightgray 1px solid;
		z-index: 2;

		-moz-transition: transform 0.6s ease;
		-webkit-transition: transform 0.6s ease;
		-o-transition: transform 0.6s ease;
		transition: transform 0.6s ease;

		background: white;

		&.visible {
			transform: translateY(0);
		}

		&.hidden {
			transform: translateY(-100%);
		}

		& img {
			height: 55px;
			float: left;
			margin: 10px;
			margin-left: 40px;
		}
	}

	// PHONE
	@media only screen and (max-width: 600px) {

		.navbar {

			& img {
				height: 30px;
				float: left;
				margin: 10px;
			}
		}
	}

	// PHONE LANDSCAPE
	@media screen and (min-width:320px) and (max-width:767px) and (orientation:landscape) {

		.navbar {

			& img {
				height: 30px;
				float: left;
				margin: 10px;
			}
		}
	}
</style>