var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"about"},[_c('div',{staticClass:"full-height show-navbar flex-middle-align"},[_c('v-layout',{attrs:{"row":"","wrap":"","justify-center":"","align-center":""}},[_c('v-flex',{attrs:{"xs12":"","md6":""}},[_c('img',{staticClass:"img-full-width observe slide-left top-page-image",attrs:{"src":require("../assets/images/about1.jpeg")}})]),_c('v-flex',{staticClass:"slide-right observe pa-4",attrs:{"xs12":"","md6":""}},[_c('p',{staticClass:"text-secondary mb-3 content-text"},[_vm._v("Poklic vzgojiteljice sem imela zelo rada, prav tako sem se rada igrala. Že kot otrok sem sama šivala oblekice za punčke, izdelovala posteljice, vozičke iz škatel za čevlje. Prvo punčko mi je prinesel Miklavž. Enkrat sem dobila klik, da bi rada tisto punčko. Seveda jo ni bilo več. Ta trenutek me je prevzela želja, da bi zbirala punčke. Šla sem na bolšjake in v starinarnice ter kupila prve punčke. Mož je dal narediti slaščičarki punčko za na torto. Naredila jo je iz marcipana in je stara 17 let. Punčke zbiram 20 let. Imam jih že 526. Sedaj mi jih podarjajo obiskovalci ali prinesejo prijatelji iz izletov iz tujine. Punčke so okrasne in igralne.")]),_c('p',{staticClass:"text-secondary mb-3 content-text"},[_vm._v("Igralne punčke so stare od 40 do 110 let. Prve so bile narejene iz papirmascheja, kasneje iz celulojda, gume, še kasneje iz plastike. Kakšen oče je izrezljal celo leseno igralno punčko, mame pa so sešile punčke iz blaga. Punčke so oblečene tako kot sem jih dobila. Redke imajo original oblačila, kajti otroci so se igrali in so se uničile. Večina punčk ima doma sešite oblekice, spletene iz volne in kvačkane.")]),_c('p',{staticClass:"text-secondary mb-3 content-text"},[_vm._v("Okrasne punčke so predvsem porcelanaste in italjanske bambule. Bambule so imeli za okras na posteljah. Skoraj v vseh gospodinjstvih so jih imeli.")]),_c('p',{staticClass:"text-secondary mb-3 content-text"},[_vm._v("Porcelanaste punčke so zelo lepega videza, imajo prekrasna oblačila, so velike in majhne. V zbirki je kar nekaj porcelanastih punčk, ki so zelo izrazne( žalostne, vesele, jezne, važne, zavistne, zaljubljene).")]),_c('p',{staticClass:"text-secondary mb-3 content-text"},[_vm._v("V zbirki so punčke iz vseh Evropskih držav.")]),_c('p',{staticClass:"text-secondary mb-3 content-text"},[_vm._v("Potem so pa še punčke oblečene v slovenske narodne noše in kot spominske iz različnih evropskih držav. ")])])],1)],1),_c('div',{staticClass:"full-height show-navbar flex-middle-align"},[_c('v-layout',{attrs:{"row":"","wrap":"","justify-center":"","align-center":""}},[_c('v-flex',{staticClass:"slide-left observe pa-4",attrs:{"xs12":"","md6":""}},[_c('p',{staticClass:"text-secondary mb-3 content-text"},[_vm._v("Kasneje sem začela zbirati vse igrače. Tako, da se je nabrala tudi zanimiva zbirka starih medvedkov, konjičkov, kuhinj, vozičkov,  didaktičnih igrač itd. ")]),_c('p',{staticClass:"text-secondary mb-3 content-text"},[_vm._v("Ljudje pa neradi zavržejo spomine na otroštvo zato so mi začeli prinašati še vse, kar so uporabljali za otroka: otroške vozičke, košare,posteljice, zibke, stoj'ce, oblačila za krst itd. ")]),_c('p',{staticClass:"text-secondary mb-3 content-text"},[_vm._v("\"Kdor zavrže igračo, zavrže otroštvo\". Zato je res bolje, da oddaste zelo stare predmete v muzeje, da se ohranijo kot kulturna dediščina, kot da jih zavržete. Predmet nima vrednosti, če je nekje na podstrehi, vrednost dobi ko je razstavljen.")])]),_c('v-flex',{staticClass:"stick-right",attrs:{"xs12":"","md6":""}},[_c('img',{staticClass:"img-full-width observe slide-right",attrs:{"src":require("../assets/images/about2.jpg")}})])],1)],1),_c('div',{staticClass:"full-height show-navbar flex-middle-align"},[_c('v-layout',{attrs:{"row":"","wrap":"","justify-center":"","align-center":""}},[_c('v-flex',{attrs:{"xs12":"","md6":""}},[_c('img',{staticClass:"img-full-width observe slide-left",attrs:{"src":require("../assets/images/about3.jpg")}})]),_c('v-flex',{staticClass:"slide-right observe pa-4",attrs:{"xs12":"","md6":""}},[_c('p',{staticClass:"text-secondary mb-3 content-text"},[_vm._v("Tudi gospodinjskih pripomočkov in kmečkega orodja nisem zavrgla. Kar veliko zanimivih predmetov je razstavljenih v kmečkem poslopju ob hiši.")]),_c('p',{staticClass:"text-secondary mb-3 content-text"},[_vm._v("Muzej je pritegnil tudi mlade: Igor Grabnar je že v rani mladosti delal makete. V hlevu hiše sem postavilia na ogled njegove makete lunaparkov, makete vlakov in avtostezo. Vse je v delovanju.")])])],1)],1),_c('div',{staticClass:"full-height show-navbar flex-middle-align"},[_c('v-layout',{attrs:{"row":"","wrap":"","justify-center":"","align-center":""}},[_c('v-flex',{staticClass:"slide-left observe pa-4",attrs:{"xs12":"","md6":""}},[_c('p',{staticClass:"text-secondary mb-3 content-text"},[_vm._v("Poleg železnic je maketar Igor Grabnar v Hiši čez cesto postavil na ogled makete, s katerimi predstavlja zgodovino Slovenije.")]),_c('p',{staticClass:"text-secondary mb-3 content-text"},[_vm._v("Otroci in mladina dobijo veliko boljšo predstavo, kateri narodi so se naseljevali na območju današnje Slovenije, kateri pomembnejši dogodki so pisali zgodovino Slovenije od Keltov do druge svetovne vojne, predstavljene so tudi pomembne osebnosti. Makete so izdelane v merilu 1:87 in 1:72, uporabljenih pa je več kot 3000 figuric.")]),_c('p',{staticClass:"text-secondary mb-3 content-text"},[_vm._v("Ogled maket je primeren za osnovne in srednje šole, ki lahko v Hiši čez cesto organizirajo tehnični ali kulturni dan. Pripravimo tudi delavnice, ki ustrezajo starostni stopnji otrok.")])]),_c('v-flex',{staticClass:"stick-right",attrs:{"xs12":"","md6":""}},[_c('img',{staticClass:"img-full-width observe slide-right",attrs:{"src":require("../assets/images/about4.jpg")}})])],1)],1)])}
