<template>
	<div class="about">
		<div class="full-height show-navbar flex-middle-align">
			<v-snackbar
					v-model="snack"
					right
					top
					:multi-line="true"
			>
				<v-icon :color="snackColor" ma-1>{{snackType}}</v-icon>
				{{ snackText }}
				<v-btn
						color="#95B277"
						flat
						@click="snack = false"
				>
					Zapri
				</v-btn>
			</v-snackbar>
				<v-flex xs12 v-if="messageSending">
					<loader></loader>
				</v-flex>
				<v-flex xs12 v-else-if="messageSent">
					<v-icon color="#95B277" class="jumbo">check_circle</v-icon>
					<p class="headline text-secondary content-text mb-0">Najlepša hvala za poslano sporočilo.</p>
					<p class="headline text-secondary content-text">Odgovor boste prejeli v najkrajšem možnem času.</p>
					<v-btn color="#95B277" dark large @click="goHome()">Domov</v-btn>
				</v-flex>
				<v-form @submit="sendEmail($event)" ref="form" v-else>
					<v-layout row wrap justify-center align-center>
					<v-flex xs12>
						<p class="headline text-secondary content-text mb-0">Želite obiskati Hišo čez cesto? Imate morda dodatna vprašanja?</p>
						<p class="headline text-secondary content-text">Preko spodnjega obrazca, nam lahko pošljete sporočilo.</p>
					</v-flex>
					<v-flex xs12>
						<v-text-field
								placeholder="Ime in priimek"
								v-model="userName"
								:rules="nameRules"
						></v-text-field>
					</v-flex>
					<v-flex xs12>
						<v-text-field
								placeholder="E-pošta"
								v-model="userEmail"
								:rules="mailRules"
						></v-text-field>
					</v-flex>
					<v-flex xs12>
						<v-textarea
								placeholder="Sporočilo..."
								v-model="message"
								:rules="messageRules"
						></v-textarea>
					</v-flex>
					<v-flex xs12 sm6 md6>
						<div class="g-recaptcha" id="recaptcha-container"
							 data-sitekey="6Leu26IUAAAAAFOrk6uBtwN6me-Q9z2MxuJdVq0n"></div>
					</v-flex>
					<v-flex xs12 sm6 md6>
						<v-btn type="submit" color="#95B277" dark large>Pošlji</v-btn>
					</v-flex>
					</v-layout>
				</v-form>
		</div>
	</div>
</template>

<script>
	import emailjs from 'emailjs-com';
	import Loader from '../components/generic/Loader';

	export default {
		name: 'Contact',
		components: { Loader },
		data() {
			return {
				userName: '',
				nameRules: [
					v => !!v || 'Ime in priimek ne smeta biti prazna',
				],
				userEmail: '',
				mailRules: [
					v => !!v || 'E-pošta ne sme biti prazna',
					v => /.+@.+/.test(v) || 'E-poštni naslov ni veljaven'
				],
				message: '',
				messageRules: [
					v => !!v || 'Sporočilo ne sme biti prazno'
				],
				snackText: '',
				snackType: '',
				snack: false,
				snackColor: '',
				messageSending: false,
				messageSent: false
			}
		},
		mounted: function () {
			setTimeout(function () {
				grecaptcha.render('recaptcha-container');
			}, 500);
		},
		methods: {
			sendEmail(event) {
				event.preventDefault();

				const vm = this;

				const valid = vm.validateForm();

				if (valid) {
					const captchaResponse = window.grecaptcha.getResponse();

					if (!captchaResponse) {

						vm.snackText = "Prosimo, potrdite, da niste robot.";
						vm.snackColor = "red lighten-2";
						vm.snackType = "error";
						vm.snack = true;

					} else {

						const templateParams = {
							from_name: vm.userName,
							from_mail: vm.userEmail,
							message_html: vm.message
						};

						vm.clearForm();

						vm.messageSending = true;
						emailjs.send('dev_test_service', 'dev_test_hisa_cez_cesto', templateParams, 'user_dXbYpMOKZi73uIQO8onU0')
							.then((response) => {

								vm.clearForm();
								vm.messageSending = false;
								vm.messageSent = true;

							}, (err) => {

								vm.messageSending = false;

								vm.snackText = "Pri pošiljanju sporočila je prišlo do napake.";
								vm.snackType = "error";
								vm.snackColor = "red lighten-2";
								vm.snack = true;

							});
					}

				} else {

					if (!vm.userName || !vm.userEmail || !vm.message) {

						vm.snackText = "Prosimo, vpišite manjkajoče podatke";
						vm.snackType = "error";
						vm.snackColor = "red lighten-2";
						vm.snack = true;

						return;
					}

					const mailPattern = /.+@.+/;

					if (!vm.userEmail.match(mailPattern)) {

						vm.snackText = "Vpišite veljaven E-poštni naslov";
						vm.snackType = "error";
						vm.snackColor = "red lighten-2";
						vm.snack = true;

					}

				}
			},
			validateForm() {
				const vm = this;

				return vm.$refs.form.validate()
			},
			clearForm() {
				const vm = this;

				vm.$refs.form.reset()
			},
			goHome() {
				const vm = this;

				vm.$router.push({name: 'home'});
			}
		}
	}
</script>

<style lang="scss" scoped>
	form {
		padding: 120px;
	}
	i {
		margin: 10px;
	}

	// PHONE
	@media only screen and (max-width: 600px) {
		form {
			padding: 20px;
		}
	}
</style>
