<template>
		<v-app>
			<v-content>
				<top-nav></top-nav>
				<router-view/>
				<bottom-foot></bottom-foot>
			</v-content>
		</v-app>
</template>

<script>
	require('intersection-observer');

	import Navbar from './components/generic/Navbar.vue';
	import Footer from './components/generic/Footer.vue';
	import TopNav from "./components/generic/Navbar";

	export default {
		name: 'App',
		components: {
			TopNav, 'top-nav': Navbar,
			'bottom-foot': Footer
		},
		watch:{
			$route (to, from){
				window.scrollTo(0,0);
			}
		}
	}
</script>

<style lang="scss">
	#app {
		font-family: 'Avenir', Helvetica, Arial, sans-serif;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
		text-align: center;
		color: #2c3e50;
	}

	.v-content__wrap {
		overflow-y: hidden !important;
	}

	.flex-middle-align {
		display: flex;
		align-items: center;
		align-content: center;
	}

	.text-primary {
		color: #95B277;
	}

	.text-secondary {
		color: #949494;
	}

	.text-light {
		font-weight: normal !important;
		font-size: 20px !important;
	}

	.slide-left {
		transform: translateX(-50%);
		opacity: 0;
		transition: all 800ms;
	}

	.slide-right {
		transform: translateX(50%);
		opacity: 0;
		transition: all 800ms;
	}

	.fade-in {
		transform: translateX(0);
		opacity: 1.0;
		transition: all 800ms;
	}
</style>
