				</v-flex>
				<v-flex xs12 md6 class="slide-right observe pa-4">
					<p class="text-secondary mb-3 content-text">Poklic vzgojiteljice sem imela zelo rada, prav tako sem se rada igrala. Že kot otrok sem sama šivala oblekice za punčke, izdelovala posteljice, vozičke iz škatel za čevlje. Prvo punčko mi je prinesel Miklavž. Enkrat sem dobila klik, da bi rada tisto punčko. Seveda jo ni bilo več. Ta trenutek me je prevzela želja, da bi zbirala punčke. Šla sem na bolšjake in v starinarnice ter kupila prve punčke. Mož je dal narediti slaščičarki punčko za na torto. Naredila jo je iz marcipana in je stara 17 let. Punčke zbiram 20 let. Imam jih že 526. Sedaj mi jih podarjajo obiskovalci ali prinesejo prijatelji iz izletov iz tujine. Punčke so okrasne in igralne.</p>
					<p class="text-secondary mb-3 content-text">Igralne punčke so stare od 40 do 110 let. Prve so bile narejene iz papirmascheja, kasneje iz celulojda, gume, še kasneje iz plastike. Kakšen oče je izrezljal celo leseno igralno punčko, mame pa so sešile punčke iz blaga. Punčke so oblečene tako kot sem jih dobila. Redke imajo original oblačila, kajti otroci so se igrali in so se uničile. Večina punčk ima doma sešite oblekice, spletene iz volne in kvačkane.</p>
					<p class="text-secondary mb-3 content-text">Okrasne punčke so predvsem porcelanaste in italjanske bambule. Bambule so imeli za okras na posteljah. Skoraj v vseh gospodinjstvih so jih imeli.</p>
					<p class="text-secondary mb-3 content-text">Porcelanaste punčke so zelo lepega videza, imajo prekrasna oblačila, so velike in majhne. V zbirki je kar nekaj porcelanastih punčk, ki so zelo izrazne( žalostne, vesele, jezne, važne, zavistne, zaljubljene).</p>
					<p class="text-secondary mb-3 content-text">V zbirki so punčke iz vseh Evropskih držav.</p>
					<p class="text-secondary mb-3 content-text">Potem so pa še punčke oblečene v slovenske narodne noše in kot spominske iz različnih evropskih držav. </p>
				</v-flex>
			</v-layout>
