				<v-flex xs12 md6 class="slide-left observe pa-4">
					<p class="text-secondary mb-3 content-text">Kasneje sem začela zbirati vse igrače. Tako, da se je nabrala tudi zanimiva zbirka starih medvedkov, konjičkov, kuhinj, vozičkov,  didaktičnih igrač itd. </p>
					<p class="text-secondary mb-3 content-text">Ljudje pa neradi zavržejo spomine na otroštvo zato so mi začeli prinašati še vse, kar so uporabljali za otroka: otroške vozičke, košare,posteljice, zibke, stoj'ce, oblačila za krst itd. </p>
					<p class="text-secondary mb-3 content-text">"Kdor zavrže igračo, zavrže otroštvo". Zato je res bolje, da oddaste zelo stare predmete v muzeje, da se ohranijo kot kulturna dediščina, kot da jih zavržete. Predmet nima vrednosti, če je nekje na podstrehi, vrednost dobi ko je razstavljen.</p>
