					<p class="text-secondary mb-3 content-text">Muzej je pritegnil tudi mlade: Igor Grabnar je že v rani mladosti delal makete. V hlevu hiše sem postavilia na ogled njegove makete lunaparkov, makete vlakov in avtostezo. Vse je v delovanju.</p>
				</v-flex>
