					<p class="text-secondary mb-3 content-text">Poleg železnic je maketar Igor Grabnar v Hiši čez cesto postavil na ogled makete, s katerimi predstavlja zgodovino Slovenije.</p>
					<p class="text-secondary mb-3 content-text">Otroci in mladina dobijo veliko boljšo predstavo, kateri narodi so se naseljevali na območju današnje Slovenije, kateri pomembnejši dogodki so pisali zgodovino Slovenije od Keltov do druge svetovne vojne, predstavljene so tudi pomembne osebnosti. Makete so izdelane v merilu 1:87 in 1:72, uporabljenih pa je več kot 3000 figuric.</p>
