<template>
	<div>
		<div class="navbar-items-wrapper">
			<router-link to="/" :class="getClass">DOMOV</router-link>
			<router-link to="/o-nas" :class="getClass">O NAS</router-link>
			<router-link to="/zbirka" :class="getClass">ZBIRKA</router-link>
			<router-link to="/dogodki" :class="getClass">DOGODKI</router-link>
			<router-link to="/kontakt" :class="getClass">KONTAKT</router-link>
			<i :class="iconClass" @click="showDropdown(true)">dehaze</i>
		</div>
		<v-layout align-center justify-center column fill-height :class="dropdownClass">
			<v-flex xs12 pa-1 class="full-width text-xs-right">
				<i class="material-icons" @click="showDropdown(false)">clear</i>
			</v-flex>

			<v-flex xs12 pa-1>
				<router-link to="/" class="navbar-item text-secondary">DOMOV</router-link>
			</v-flex>
			<v-flex xs12 pa-1>
				<router-link to="/o-nas" class="navbar-item text-secondary">O NAS</router-link>
			</v-flex>
			<v-flex xs12 pa-1>
				<router-link to="/zbirka" class="navbar-item text-secondary">ZBIRKA</router-link>
			</v-flex>
			<v-flex xs12 pa-1>
				<router-link to="/dogodki" class="navbar-item text-secondary">DOGODKI</router-link>
			</v-flex>
			<v-flex xs12 pa-1>
				<router-link to="/kontakt" class="navbar-item text-secondary">KONTAKT</router-link>
			</v-flex>
		</v-layout>
		<div :class="dropdownBgClass"></div>
	</div>
</template>

<script>
	export default {
		name: 'NavbarItems',
		props: {
			"light": Boolean
		},
		data() {
			return {
				dropdownVisible: false
			}
		},
		computed: {
			getClass: function () {
				let baseClass = 'navbar-item text-secondary';

				if (this.light) {
					baseClass = `${baseClass} light`;
				}

				return baseClass;
			},
			iconClass: function () {
				let baseClass = 'show-menu material-icons'

				if (this.light) {
					baseClass = `${baseClass} white-text`;
				}

				return baseClass;
			},
			dropdownClass: function () {
				let cl = 'dropdown-items-wrapper';
				if (this.dropdownVisible) cl += ' visible';
				else cl += ' hidden';

				return cl;
			},
			dropdownBgClass: function () {
				let cl = 'dropdown-bg';
				if (this.dropdownVisible) cl += ' visible';
				else cl += ' hidden';

				return cl;
			}
		},
		methods: {
			showDropdown(show) {
				if (show) document.getElementsByTagName('html')[0].style.overflowY = "hidden";
				else document.getElementsByTagName('html')[0].style.overflowY = "scroll";
				this.dropdownVisible = show;
			}
		},
		watch:{
			$route (to, from){
				this.showDropdown(false);
			}
		}
	}
</script>

<style lang="scss" scoped>
	.navbar-items-wrapper {
		padding: 30px !important;
		text-align: right;

		& i.show-menu {
			display: none;
		}
	}

	.navbar-item {
		font-weight: bold;
		text-decoration: none;
		padding: 0 20px 0 20px;

		&.light {
			color: white;
		}

		&.router-link-exact-active {
			color: #95B277 !important;
		}
	}

	.dropdown-items-wrapper {
		display: none;
	}

	.dropdown-bg {
		display: none;
	}

	// TABLET
	@media only screen and (max-width: 992px) and (min-width: 600px) {
		.navbar-items-wrapper {

			& a {
				display: none;
			}

			& i.show-menu {
				display: initial;
			}
		}

		.dropdown-items-wrapper {
			//margin-top: 15% !important;
			height: 200px !important;
			display: block;

			position: fixed;
			top: 0;
			width: 100%;
			border-bottom: lightgray 1px solid;
			z-index: 2;

			-moz-transition: transform 0.6s ease;
			-webkit-transition: transform 0.6s ease;
			-o-transition: transform 0.6s ease;
			transition: transform 0.6s ease;

			background: white;

			&.visible {
				transform: translateY(0);
			}

			&.hidden {
				transform: translateY(-100%);
			}
		}

		.dropdown-bg {
			display: block;
			position: absolute;
			width: 100vw;
			height: 100vh;
			background: rgba(0,0,0,0.7);
			pointer-events: none;

			transition: all 0.6s ease;

			&.visible {
				opacity: 1;
				transform: translateY(0%);
			}

			&.hidden {
				opacity: 0;
				//visibility: hidden;
				transform: translateY(100%)
			}
		}
	}

	// PHONE
	@media only screen and (max-width: 600px) {
		.navbar-items-wrapper {
			padding: 10px !important;

