			transition: transform 0.6s ease, opacity 0.9s ease;

			&.visible {
				opacity: 1;
				transform: translateY(0%);
			}

			&.hidden {
				opacity: 0;
				//visibility: hidden;
				transform: translateY(100%)
			}
		}
	}
</style>